.grid{
    
    display: grid;
    margin-top:30px; 
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    grid-template-columns:repeat(16,1fr) ;
    width: fit-content;
    margin-left: 30px;
    margin-right: 30px;
    border:solid 4px #808080;

}
.grid-easy{
    display: grid;
    grid-template-columns:repeat(8,1fr) ;

}
.grid-hard{
    display: grid;
    grid-template-columns:repeat(30,1fr) ;
}
