.box{
    overflow: hidden;
    width: 30px;
    height: 30px;
    background-color:#B6B6B6;
    align-content: center;
    margin: auto;
    position: relative;
    vertical-align: middle;
    border-top:solid 4px #808080;
    border-left:solid 4px #808080;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    text-align: center;
}
.start-body{
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    animation-name: end;
    animation-fill-mode: forwards;
    animation-delay: 1s;
    z-index: 1;
}
@keyframes end{
    from{
        background-color: none;
    }to{
        background-color: rgba(65,65,65,0.5);
    }
}
.one{
    
    color: blue;
    font-size: 25px;
}
.two{
    color: green;
    font-size: 25px;
}
.three{
    color:red;
    font-size: 25px;
}
.four{
    color:yellow;
    font-size: 25px;
}
.five{
