}
.six{
    color: mediumvioletred;
    font-size: 25px;
}
.seven{
    color: turquoise;
    font-size: 25px;
}
.eight{
    color:gray;
    font-size: 25px;
}
.result{
    background-color: #D5D5D6;
    color: #2E2F30;
    position: absolute;
    z-index: 3;
    top:10%;
    text-align: center;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    left:0;
    right:0;
    font-size: 0;
    margin-left:auto;
    animation-name: result;
    animation-duration:3s; 
    animation-iteration-count: 1; 
    animation-delay: 1s;
    margin-right:auto;
    border-radius: 5px;
    margin: auto; 
    animation-fill-mode: forwards;
}
@keyframes result{
    from{
        width: 0px;
        font-size: 0;
        height: 0px;
    }to{
        padding-top: 20px;
        width: 200px;
        font-size: 30px;
        height:200px;
    }
}
.start-btn{
    
    background-color: grey;
    color: white;
    position: absolute;
    z-index: 3;
    font-family: Helvetica, sans-serif;
    height: 0px;
    width:0;
    top:50%;
    left:0;
    animation-delay: 1s;
    right:0;
    border: 0;
    border-radius: 2px;
    margin-left:auto;
    margin-right:auto;
    margin: auto; 
    font-size: 0px;
    animation-name: start;
    animation-duration: 3s;
    animation-fill-mode: forwards;
    
    border:2px solid #262728;
    border-radius: 2px;

}
@keyframes start{
    from{
        width: 0px;
        font-size: 0;
        height: 0px;

    }to{
        width: 200px;
        color: white;
        font-size: 30px;
        height:100px;
    }
}
.start-btn:hover{
    cursor: pointer;
}

.flag{
    font-family:  Helvetica, sans-serif;
