}
  .btn-flex{
    
    display: flex;
    align-items: stretch;
      width: 100%;
  }
.dropdown {
    text-align: center;
    background-color: #EAEAEB;
    border: 1px solid black ;
    height:30px;
    width:100px;
    display: inline-block;
} 

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: fit-content;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}
.dropdown-content button{
    display: block;
    width: 100px;
    background-color: #959799;
    border: none;
}
.dropdown-content button:hover{
    background-color: #2E2F30;
    color:white;
}
  
.dropdown:hover .dropdown-content {display:block;}

.start-btn:hover{
    transform: rotate(-5deg);
}
.timer{
    font-family:  Helvetica, sans-serif;
    font-size: 30PX;
    margin: 0;
    border: 1px solid black ;
    background-color: #EAEAEB;
    border-radius: 3px;
    width: fit-content;
    height: fit-content;
    text-align:center; 
    display: inline-block;
}
.btn-grid{
    position: relative;
    width: fit-content;

    margin: auto;
    top:20px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
}
.result img{
    animation-name: imgA;
    height: 0;
    width:0;
    animation-delay: 1s;
    animation-duration: 3s;
    animation-fill-mode: forwards;
}

@keyframes imgA{
    from{
        width: 0;
        height: 0;
    }to{
        width: 30px;
        height: 30px;
    }
}